import "./styles.css";

const addAtodoBtns = document.querySelectorAll(".add-todo-item");
const todos = document.querySelectorAll(".todos")
const cancleTodoBtns = document.querySelectorAll(".cancle-todo-btn")
const sideBar = document.querySelector(".side-bar")
const sideBarMenu = document.querySelector(".side-bar-menu")
const hideSideBar = document.querySelector("#slide-in");
const todoTitleInputs = document.querySelectorAll(".todo-title-input");







hideSideBar.addEventListener("click", () => {
    sideBar.classList.add("hide") 
});

sideBarMenu.addEventListener("click", () => {
    sideBar.classList.remove("hide") 
});

let isTodoInfoFormShown = false;



const observer = new MutationObserver((mutations) => {
    mutations.forEach((mutation) => {
        const newNode = mutation.addedNodes[0];

        console.log('New node added:', newNode);
    });
});

const config = { childList: true };




todos.forEach((list) => {
    observer.observe(list, config)
});



cancleTodoBtns.forEach((cancleBtn, index) => {
    cancleBtn.addEventListener("click", () => {
        if(isTodoInfoFormShown){
           
            todos[index].querySelector(".todo-item-info").classList.remove("show")
            isTodoInfoFormShown = false
        }
    })
});



addAtodoBtns.forEach((btn, index) => {
    btn.addEventListener("click", (e) => {
      
        if(isTodoInfoFormShown === false){
            todos[index].querySelector(".todo-item-info").classList.add("show")
            isTodoInfoFormShown = true;
        } 
        
        console.log("Hey: ", btn , index)
    })
});


todoTitleInputs.forEach((titleInput, index) => {
    titleInput.addEventListener("keydown", (e) => {
        if(e.key === "Escape"){
            todos[index].querySelector(".todo-item-info").classList.remove("show")
            titleInput.value = "";
            isTodoInfoFormShown = false;
        }

        if(e.key === "Enter" && titleInput.value.trim() !== ""){
            const todoTitles = JSON.parse(localStorage.getItem("todoTitles"));
            console.log(todoTitles)
            todoTitles.push(titleInput.value);
            localStorage.setItem("todoTitles", JSON.stringify(todoTitles));
            makeTodoAndAppendIt(todos[index], titleInput.value, todos[index].querySelector(".todo-item-info"))
            todos[index].querySelector(".todo-item-info").classList.remove("show")
            titleInput.value = "";
            isTodoInfoFormShown = false;
        }
    });
});





function makeTodoAndAppendIt(parent, todoTitleInput, addTodoBtn){
    const todoContainer = document.createElement("div");
    todoContainer.classList.add("todo-item");
    const checkBox = document.createElement("div");
    checkBox.classList.add("check-box");
    const todoTitle = document.createElement("div");
    todoTitle.classList.add("check-box-title");

    todoTitle.innerText = todoTitleInput;

    todoContainer.append(checkBox, todoTitle);

    parent.insertBefore(todoContainer, addTodoBtn);

}




window.onload = function(){
    if(localStorage.getItem("todoTitles")){
        const storedTitles = JSON.parse(localStorage.getItem("todoTitles"));
        console.log(storedTitles)
        for(let title of storedTitles){
            console.log(title)
            makeTodoAndAppendIt(todos[0], title, todos[0].querySelector(".todo-item-info"));
        }
       
    } else {
        console.log("hi")
        let todoTitles = ['Play some football', 'Studying', 'Have Some fun'];
        localStorage.setItem("todoTitles", JSON.stringify(todoTitles));
        const storedArrOfTitles = JSON.parse(localStorage.getItem("todoTitles"));
        for(let title of storedArrOfTitles){
            console.log(title)
            makeTodoAndAppendIt(todos[0], title, todos[0].querySelector(".todo-item-info"));
        }
    }
    
}

