___CSS_LOADER_EXPORT___.push([module.id, `::after, ::before, * {
    box-sizing: border-box;
    font-family: "roboto", sans-serif;
}

body {
    margin: 0;
}


.main-content {
    position: relative;
    width: 100vw;
    height: 100vh;
    display: flex;
    background-color: #252422;
    
}

.side-bar {
    z-index: 2;

    width: 310px;
    min-height: 100vh;
    padding: 30px 20px;
    overflow: auto;
    color: white;
    background-color: #111113;
    border-radius: 0 15px 0 0;
    border-right: 1px solid #16425b77;
    transition: all 0.4s ease;
}
.side-bar-menu {
    
    margin: 20px 25px;
    cursor: pointer;
    position: absolute;
    z-index: 1;
    color: rgb(255, 255, 255);
    transition: all 0.3s ease;
}

.side-bar-menu:hover {
    color: #708d81;
}



.side-bar.hide {
    transform: translateX(-380px);
}

.side-bar::-webkit-scrollbar {
    width: 10px; /* set scrollbar width */
    height: 10px;
}

.header {
    display: flex;
    /* border: 1px solid red; */
    justify-content: space-between;
    align-items: center;
}

.header > h2 {
    font-size: 24px;
    letter-spacing: 3px;
    margin: 0;
}
.header-icons {
    display: flex;
    gap: 25px;
    justify-content: center;
    align-items: center;
}

#slide-in {
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 21px;
    
}

#add-project-icon {
    padding: 4px;
    background-color: #ffffff1e;
    cursor: pointer;
    border-radius: 5px;
    transition: all 0.3s ease;
}

#add-project-icon:hover {
    background-color: #ffffff3f;
}

#slide-in:hover {
   color: #708d81;
   transform: translateX(-5px);
}





.projects {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    gap: 18px;
    overflow: hidden;

}

.project {

    width: 100%;
    height: fit-content;
    
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    gap: 15px;
    padding: 5px 15px;
    /* border: 1px solid rgba(255, 255, 255, 0.151); */
    background-color: #202d4665;
}

.project-title-add-project {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#remove-project-icon {
    transition: all 0.3s ease;
    cursor: pointer;
    
    font-size: 24px;
}


#remove-project-icon:hover {
    color: #a3cef1;
}

.project-title {
    font-size: 19.5px;
    letter-spacing: 2px;
    color: #acad94;
    margin: 0;
    margin-top: 15px;
    margin-bottom: 8px;
}


.todos {
   
    display: flex;
    margin-top: -6px;
    flex-direction: column;
    padding: 0 2px;
    gap: 0;
}
.todo-item {
    margin: 0;
    display: flex;
    align-items: center;
    margin-bottom: 15px;
    gap: 10px;
    /* border: 1px solid red; */
    
    
}

.add-todo-item {
    margin: 0;
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    gap: 10px;
    cursor: pointer;
    
}

.add-todo-item > .check-box {
    display: flex;
    width: 23px;
    height: 23px;
    justify-content: center;
    align-items: center;
    text-align: center;
    
    border: 1px solid #363532;
    padding: 8px;
}

.add-todo-item > .check-box > span {
    font-size: 20px;
    text-align: center;
}

.add-todo-item > .check-box-title {
    font-size: 19px;
    letter-spacing: 1px;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: #504e4b;

    
}
  
.check-box {
    
    width: 19px;
    cursor: pointer;
    height: 19px;
    border: 1px solid rgb(107, 107, 107);
    border-radius: 5px;
}

.check-box-title {
    font-size: 16px;
    letter-spacing: 1px;
    width: 100%;
    color: #adb5bd;
    cursor: pointer;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  
}

.cancle-todo-btn {
    cursor: pointer;
    color: #d90429;
    font-size: 20px;
    transition: color 0.3s ease;
    /* border: 1px solid white;
    width: 18px;
    height: 18px; */
    
    
}

.cancle-todo-btn:hover {
    color: #ee4360;
}

.todo-title-input {
    height: 34px;
    padding: 0 7px;
    width: 180px;
    letter-spacing: 1px;
    color: white;
    border: 1px solid #2b2d42e3;
    font-size: 16px;
    
    color: #adb5bd;
    outline: none;
    background-color: #262931a2;
  

}

.todo-item-info {
    display: none;
}

.todo-item-info.show {
    display: flex;
}







.tasks-page {
    display: inline-block;
    width: calc(100vw - 385px);
    height: 100vh;
    background-color: #252422;
    padding: 60px 60px;
    color: #e0e1dd;

}

.title-for-todo {
    font-size: 26px;
    letter-spacing: 0.8px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,iCAAiC;AACrC;;AAEA;IACI,SAAS;AACb;;;AAGA;IACI,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,aAAa;IACb,yBAAyB;;AAE7B;;AAEA;IACI,UAAU;;IAEV,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;IACd,YAAY;IACZ,yBAAyB;IACzB,yBAAyB;IACzB,iCAAiC;IACjC,yBAAyB;AAC7B;AACA;;IAEI,iBAAiB;IACjB,eAAe;IACf,kBAAkB;IAClB,UAAU;IACV,yBAAyB;IACzB,yBAAyB;AAC7B;;AAEA;IACI,cAAc;AAClB;;;;AAIA;IACI,6BAA6B;AACjC;;AAEA;IACI,WAAW,EAAE,wBAAwB;IACrC,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,2BAA2B;IAC3B,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,mBAAmB;IACnB,SAAS;AACb;AACA;IACI,aAAa;IACb,SAAS;IACT,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,eAAe;IACf,yBAAyB;IACzB,eAAe;;AAEnB;;AAEA;IACI,YAAY;IACZ,2BAA2B;IAC3B,eAAe;IACf,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;GACG,cAAc;GACd,2BAA2B;AAC9B;;;;;;AAMA;IACI,gBAAgB;IAChB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,gBAAgB;;AAEpB;;AAEA;;IAEI,WAAW;IACX,mBAAmB;;IAEnB,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,iBAAiB;IACjB,kDAAkD;IAClD,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,eAAe;;IAEf,eAAe;AACnB;;;AAGA;IACI,cAAc;AAClB;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,cAAc;IACd,SAAS;IACT,gBAAgB;IAChB,kBAAkB;AACtB;;;AAGA;;IAEI,aAAa;IACb,gBAAgB;IAChB,sBAAsB;IACtB,cAAc;IACd,MAAM;AACV;AACA;IACI,SAAS;IACT,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,SAAS;IACT,2BAA2B;;;AAG/B;;AAEA;IACI,SAAS;IACT,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,SAAS;IACT,eAAe;;AAEnB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,YAAY;IACZ,uBAAuB;IACvB,mBAAmB;IACnB,kBAAkB;;IAElB,yBAAyB;IACzB,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,mBAAmB;IACnB,oGAAoG;IACpG,cAAc;;;AAGlB;;AAEA;;IAEI,WAAW;IACX,eAAe;IACf,YAAY;IACZ,oCAAoC;IACpC,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,mBAAmB;IACnB,WAAW;IACX,cAAc;IACd,eAAe;IACf,gBAAgB;IAChB,uBAAuB;IACvB,mBAAmB;;AAEvB;;AAEA;IACI,eAAe;IACf,cAAc;IACd,eAAe;IACf,2BAA2B;IAC3B;;mBAEe;;;AAGnB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,cAAc;IACd,YAAY;IACZ,mBAAmB;IACnB,YAAY;IACZ,